import Vue from 'vue'
import VueRouter from 'vue-router'
// import Login from '@/views/login'
import Layout from '@/views/layout'
// import Search from '@/views/search/index.vue'
// import SearchList from '@/views/search/list.vue'
// import Prodetail from '@/views/prodetail'
// import Pay from '@/views/pay'
// import MyOrder from '@/views/myorder'
import Home from '@/views/layout/home.vue'
import User from '@/views/layout/user.vue'
import Cart from '@/views/layout/cart.vue'
import CateGory from '@/views/layout/category.vue'
// import Address from '@/views/pay/address'

import store from '@/store'

// 设置js懒加载减少功耗
const Login = () => import('@/views/login')
const Search = () => import('@/views/search')
const SearchList = () => import('@/views/search/list')
const Prodetail = () => import('@/views/prodetail')
const Pay = () => import('@/views/pay')
const MyOrder = () => import('@/views/myorder')
const Address = () => import('@/views/pay/address')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    // 登录
    { path: '/login', component: Login },
    // 首页
    {
      path: '/',
      component: Layout,
      // 重定向
      redirect: '/home',
      children: [
        // 首页
        { path: '/home', component: Home },
        // 分类
        { path: '/category', component: CateGory },
        // 购物车
        { path: '/cart', component: Cart },
        // 用户
        { path: '/user', component: User }

      ]
    },
    // 搜索页
    { path: '/search', component: Search },
    // 搜索列表
    { path: '/searchlist', component: SearchList },
    // 商品详情   动态路由传参，确认将来是那个商品，路由参数中需要携带 id
    { path: '/prodetail/:id', component: Prodetail },
    // 结算支付
    { path: '/pay', component: Pay },
    // 添加收货地址
    { path: '/address', component: Address },
    // 订单管理
    { path: '/myorder', component: MyOrder }

  ]
})

// 所有的路由在真正被访问到之前（解析渲染对应组件页面前），都会先经过全局前置守卫
// 只有全局前置守卫放行了，才会到达对应的页面

// 全局前置导航守卫
// to: 到哪去，到哪去的完整路由信息对象（路径，参数）
// from: 从哪来，从哪来的完整路由信息对象（路径，参数）
// next：是否放行，
// （1）next（）直接放行，放行到to要去的路径
// （2）next（路径） 进行拦截，拦截到next里面配置的路径

// 定义一个数组，专门用户存放所有需要权限访问的页面
const authUrls = ['/pay', '/myorder']
router.beforeEach((to, from, next) => {
  // console.log(to, from, next)
  // 看to.path是否在authUrls中
  if (!authUrls.includes(to.path)) {
    next()
    return
  }
  // 是权限界面，需要判断token
  const token = store.getters.token
  if (token) {
    next()
  } else {
    next('/login')
  }
})
export default router
