import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      // 个人权证相关
      userInfo: getInfo()
    }
  },
  // 方法
  mutations: {
    // 所有mutations 的第一个参数都是state
    setUserInfo (state, obj) {
      state.userInfo = obj
      // 存入到本地
      setInfo(obj)
    }
  },
  // 异步
  actions: {
    logout (context) {
      // 个人信息要重置
      context.commit('setUserInfo', {})
      // 购物车信息要重置(跨模块调用mutation)cart/setcartlist
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  // 派生属性
  getters: {}
}
